import React, { useState } from 'react'
import { Form, Input, Modal, message } from 'antd'
import { Button } from '@chakra-ui/react'
import './style.scss'
import { gql } from 'apollo-boost'
import { useMutation, useLazyQuery } from '@apollo/react-hooks'
import { setToLocalStorage } from '../../utils'
import { updateUser } from '../../store/user/actions'
import { setContext } from '@apollo/client/link/context'
import { useDispatch } from 'react-redux'

// ÁôªÂΩï
const SIGN_IN = gql`
    mutation signIn($email: String!, $password: String!) {
      signIn(input: {email: $email, password: $password}) {
        token
        user {
          _id email avatar role verified name
        }
      }
    }
`

// Ê≥®ÂÜå
const SIGN_UP = gql`
  mutation signUp($email: String!, $password: String!, $name: String!) {
    signUp( input: {email: $email,password: $password,name: $name
    }) {
      token
      user {
        _id email avatar role verified name
      }
    }
  }
`

// Êü•ËØ¢Ë¥¶Âè∑ÊòØÂê¶Â∑≤Ê≥®ÂÜå
const QUERY_EMAIL_STATUS = gql`
    query queryEmail($email: String!) {
      email(email:$email) {
        available
      }
    }
`

const Login = () => {
  const [signIn] = useMutation(SIGN_IN)
  const [signUp] = useMutation(SIGN_UP)
  const [queryEmail, { data: emailStatus }] = useLazyQuery(QUERY_EMAIL_STATUS)

  const [form] = Form.useForm()

  const [visible, setVisible] = useState(false)
  const dispatch = useDispatch()

  const handleSetVisible = (val:boolean) => {
    setVisible(val)
  }

  const changeLoginState = (data: any) => {
    setVisible(false)
    setToLocalStorage('token', data.token)
    const { _id, email, avatar, role, verified, name, desc } = data.user
    const user = { id: _id, email, avatar, name, role, verified, desc }
    setToLocalStorage('me', user)
    dispatch(updateUser(user))
    setContext((_, { headers }) => {
      return {
        headers: {
          ...headers,
          authorization: data.token
        }
      }
    })
  }
  const onFinish = async (values:any) => {
    const { email, password, name } = values
    if (!emailStatus?.email?.available) {
      await signIn({ variables: { email, password } })
        .then(({ data }) => {
          changeLoginState(data.signIn)
        })
        .catch(err => {
          throw err
        })
    } else {
      await signUp({ variables: { email, password, name } })
        .then(({ data }: any) => {
          changeLoginState(data.signUp)
        })
        .catch(err => {
          throw err
        })
    }
  }

  const getEmailStatus = async () => {
    await queryEmail({ variables: { email: form.getFieldsValue().email } })
  }

  return (<div className={'login'}>
    <Button className={'login-btn'} onClick={handleSetVisible.bind(null, true)}>ÁôªÂΩï/Ê≥®ÂÜå</Button>
    <Modal
      title= { emailStatus && !emailStatus?.email?.available ? 'Ê≥®ÂÜå' : 'ÁôªÂΩï'}
      className={'login-modal'}
      visible={visible}
      width={350}
      footer={null}
      onCancel={handleSetVisible.bind(null, false)}
    >
      <Form
        form={form}
        name={'login'}
        onFinish={onFinish}
      >
        <Form.Item
          name="email"
          rules={[{ required: true, message: 'ËØ∑ËæìÂÖ•ÈÇÆÁÆ±!' }]}>
          <Input placeholder={'ËØ∑ËæìÂÖ•ÈÇÆÁÆ±Ë¥¶Âè∑'} allowClear onBlur={ async () => {
            await getEmailStatus()
          }}/>
        </Form.Item>
        {
          emailStatus && !emailStatus?.email?.available ? <Form.Item
            name='name'
            rules={[{ required: true, message: 'ËØ∑ËÆæÁΩÆÁî®Êà∑Âêç!' }]}>
            <Input placeholder={'Ëøô‰∏™ÈÇÆÁÆ±ËøòÊ≤°Ê≥®ÂÜåÂì¶ÔºåËØ∑ËÆæÁΩÆÁî®Êà∑ÂêçÂÆåÊàêÊ≥®ÂÜå'} allowClear/>
          </Form.Item> : null
        }
        <Form.Item
          name="password"
          rules={[{ required: true, message: 'ËØ∑ËæìÂÖ•ÂØÜÁ†Å!' }]}
        >
          <Input.Password placeholder={'ËØ∑ËæìÂÖ•ÂØÜÁ†Å'} allowClear/>
        </Form.Item>
        <Form.Item className={'login-button'}>
          <Button type={'submit'}>
            { emailStatus && !emailStatus?.email?.available ? 'Ê≥®ÂÜå' : 'ÁôªÂΩï'}
          </Button>
        </Form.Item>
      </Form>
      <a className={'forget-password'}>ÂøòËÆ∞ÂØÜÁ†Å</a>
    </Modal>
  </div>)
}
export default Login
